<template>
  <section>
    <div>
      Effect type:
      <select v-model="effect.type">
        <option v-for='(val, key) in types' :key='val' :value="key">{{val}}</option>
      </select>
      
      <input v-model="effect.tick" type="number" placeholder="Tick" />
      <input v-model="effect.duration" type="number" placeholder="Duration" />
      <input v-model="effect.value" type="number" placeholder="Value" />

      Conditions:
      <select v-model="effect.condition">
        <option v-for="(x, key) in condition" :key="x" :value="key">{{x}}</option>
      </select>
      
      Attributes:
      <select v-model="effect.attr">
        <option v-for="(x, key) in attr" :key="x" :value="key">{{x}}</option>
      </select>
      
      Trigger:
      <select v-model="effect.trigger">
        <option v-for="(x, key) in trigger" :key="key" :value="key">{{x}}</option>
      </select>
      
      Auto-target:
      <select v-model="effect.auto_target">
        <option v-for="(x, key) in auto_target" :key="x" :value="key">{{x}}</option>
      </select>
    </div>
  </section>
</template>

<script>
export default {
  props: ["effects"],
  data() {
    return {
      types:["Reduce", "Increase"],
      attr: ["None", "HP", "Damage", "Cooldown", "Energy", "Skill"],
      condition: ["None", "Target Ally", "Target Enemy", "Target Self"],
      trigger: [
        "None",
        "onHit",
        "onUse",
        "onDeath",
        "onSpecial",
        "onPhysical",
        "onStatus"
      ],
      auto_target:["None", "Random Ally", "Random Enemy", "Random", "All Enemies", "All Allies", "Self"],
      effect: this.effects
    };
  },
  watch: {
    "effect.duration"(n) {
      this.effect.duration = parseInt(n);
    },
    "effect.tick"(n) {
      this.effect.tick = parseInt(n);
    },
    "effect.value"(n) {
      this.effect.value = parseInt(n);
    },
    "effect.type"(_new) {
      this.effect.type = parseInt(_new);
    },
    "effect.condition"(n) {
      this.effect.condition = parseInt(n);
    },
    "effect.attr"(n) {
      this.effect.attr = parseInt(n);
    },
    "effect.trigger"(n) {
      this.effect.trigger = parseInt(n);
    },
    "effect.auto_target"(n) {
      this.effect.auto_target = parseInt(n);
    }
  }
};
</script>