<template>
  <section>
    <h3>Create a skill</h3>
    <input type="file" @change="onChange" />
    <section>
      <input v-model="skill.skillName" type="text" placeholder="skill name" />
      <input v-model="skill.description" type="text" placeholder="skill description" />
      <input v-model="skill.cooldown" type="number" placeholder="cooldown" />
    </section>

    <div>
      Target selection
      <select v-model="skill.selection">
        <option v-for="(val, key) in selection" :key="key" :value="key">
          {{ val }}
        </option>
      </select>
    </div>

    <section>
      Energy Cost:
      <div v-for="x in skill.costs.length"
        :key="x">
        {{ costs[x-1] }} :
        <input v-model="skill.costs[x-1]" type="number" />
      </div>
    </section>

    <button @click="skill.effects.push({})">Add Effect</button>
    {{ skill.effects }}
    <div v-for="x in skill.effects.length" :key="x">
      <SkillEffect :effects="skill.effects[x-1]" />
      <button @click="skill.effects.splice(x-1,1)">Delete</button>
    </div>
    <button @click="save">Save</button>
    <a @click="$router.go(-1)">Go Back.</a>
  </section>
</template>

<script>
import SkillEffect from "./components/SkillEffect";
export default {
  components: {
    SkillEffect
  },
  data() {
    return {
      skill: {
        skillName: null,
        description: null,
        cooldown: null,
        selection: null,
        costs: [0, 0, 0, 0, 0],
        effects: []
      },
      skillpic: null,
      imageUrl: null,
      costs:["Strength", "Agility", "Wisdom", "Spirit", "Random"],
      selection:["Enemy", "Ally", "Self", "All enemies", "All allies", "Random"]
    };
  },
  methods: {
    onChange(ev) {
      this.skillpic = ev.target.files[0];
      this.imageUrl = URL.createObjectURL(this.skillpic);
    },
    save() {
      this.skill.selection = parseInt(this.skill.selection)
      this.skill.target = parseInt(this.skill.selection)
      this.skill.costs = this.skill.costs.map(Number)

      this.$store.commit("character/saveSkill", {skill: this.skill, facepic:  this.skillpic})
      this.skill ={
        skillName: null,
        description: null,
        cooldown: null,
        costs: [0, 0, 0, 0, 0],
        selection: null,
        effects: []
      },
      this.skillpic =  null
      this.imageUrl =  null
    }
  }
}
</script>